.NavBar__List {
  list-style: none;

}

.NavBar__Item {
  text-decoration: none;
  color: rgb(255, 255, 255);
  position: relative;

}

  /* background-color: brown ; */
  /* margin: -10px 0; */


  .NavBar__Item:before{
    content: "";
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 45px;
  left: 0;
  background-color: rgb(255, 255, 255);
  visibility: hidden;
  transition: all 0.4s ease-in-out;
  }


  .NavBar__Item:hover:before {
    visibility: visible;
    width: 100%;
  }

  .NavBar__Item:hover {
    transition: all 0.4s ease-in-out;
  }
